body {
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #252628;
  color: white;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
}

h1 {
  font-family: 'Raleway', sans-serif;
  text-align: center;
  font-weight: 100 !important;
  font-size: 2em !important;
}

.spidr-form {
  flex: 1;
  display: flex;
  height: 100vh;
  position: relative;
}

.form-div {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10%;
  position: relative;
  margin-left: -20%;
  z-index: 2;
}

.form-wrapper {
  width: 100%;
  /* max-width: 500px; */
  background: rgba(71, 157, 175, 0.9);
  padding-left: 30%;
  padding-right: 30%;
  padding-bottom: 10%;
}

.air-fryer-image {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  z-index: 1;
  /* flex: 1; */
}

.air-fryer-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(90%);
}


@media (max-width: 1000px) {
  .spidr-form {
    position: relative;
    flex-direction: column;
    height: 100vh;
  }

  .form-div,
  .air-fryer-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .form-div {
    z-index: 2;
    align-items: center;
    justify-content: center;
    padding: 5rem;
  }

  .form-wrapper {
    /* background: white;
    color: black;
    border-radius: 12px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
    max-width: 400px;
    padding: 2rem;
    width: 90%; */
  }

  .air-fryer-image {
    z-index: 1;
  }

  .air-fryer-image img {
    object-fit: cover;
    filter: grayscale(100%);
  }
}